import React, {useState, useContext} from 'react'
import {useSkin} from '@hooks/useSkin'
import {connect} from 'react-redux'
import {AbilityContext} from '@src/utility/context/Can'
import {Link, useHistory} from 'react-router-dom'
import InputPasswordToggle from '@components/input-password-toggle'
import {AvForm, AvInput} from 'availity-reactstrap-validation-safe'
import {
    Row,
    Col,
    CardTitle,
    CardText,
    FormGroup,
    Label,
    Button
} from 'reactstrap'
import s from './Login.module.css';
import '@styles/base/pages/page-auth.scss'
import {login} from "../../../redux/reducers/auth-reducer";
import logo from '../../../assets/images/logo.png';
const Login = props => {
    const [skin, setSkin] = useSkin()
    const ability = useContext(AbilityContext)
    const history = useHistory()
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const illustration = skin === 'dark' ? 'login-v2-dark.svg' : 'login-v2.svg',
        source = require(`@src/assets/images/pages/${illustration}`).default

    const handleSubmit = (event, errors) => {
        if (errors && !errors.length) {
            props.login({email, password}, history, ability);
        }
    }

    return (
        <div className='auth-wrapper auth-v2'>
            <Row className='auth-inner m-0'>
                <a className={`brand-logo ${s.logo}`} href='https://buildtheworld.ru/'>
                    <img src={logo} alt=""/>
                </a>
                <Col className='d-none d-lg-flex align-items-center p-5' lg='8' sm='12'>
                    <div className='w-100 d-lg-flex align-items-center justify-content-center px-5'>
                        <img className='img-fluid' src={source} alt='Login V2'/>
                    </div>
                </Col>
                <Col className='d-flex align-items-center auth-bg px-2 p-lg-5' lg='4' sm='12'>
                    <Col className='px-xl-2 mx-auto' sm='8' md='6' lg='12'>
                        <CardTitle tag='h2' className='font-weight-bold mb-1'>
                            –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å! üëã
                        </CardTitle>
                        <CardText className='mb-2'>–ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –∞–≤—Ç–æ—Ä–∏–∑—É–π—Ç–µ—Å—å —á—Ç–æ–±—ã –ø–æ–ø–∞—Å—Ç—å –≤ –ø–∞–Ω–µ–ª—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞</CardText>
                        <AvForm className='auth-login-form mt-2' onSubmit={handleSubmit}>
                            <FormGroup>
                                <Label className='form-label' for='login-email'>
                                    Email
                                </Label>
                                <AvInput
                                    required
                                    autoFocus
                                    type='email'
                                    value={email}
                                    id='login-email'
                                    name='login-email'
                                    placeholder='john@example.com'
                                    onChange={e => setEmail(e.target.value)}
                                />
                            </FormGroup>
                            <FormGroup>
                                <div className='d-flex justify-content-between'>
                                    <Label className='form-label' for='login-password'>
                                        –ü–∞—Ä–æ–ª—å
                                    </Label>
                                </div>
                                <InputPasswordToggle
                                    required
                                    tag={AvInput}
                                    value={password}
                                    id='login-password'
                                    name='login-password'
                                    className='input-group-merge'
                                    onChange={e => setPassword(e.target.value)}
                                />
                            </FormGroup>
                            <Button.Ripple color='primary' block disabled={!email.length || !password.length}>
                                –í–æ–π—Ç–∏
                            </Button.Ripple>
                        </AvForm>
                    </Col>
                </Col>
            </Row>
        </div>
    )
}

export default connect('', {login})(Login);
